





























    import mInput from '../../components/m-input.vue';
	import {mapState, mapMutations} from 'vuex'
	

    export default {
        components: {
            mInput
        },
		computed: mapState(['forcedLogin','accounts', 'redirect']),
        data() {
            return {
				bg:this.$http.imgPath + "manage/register-bg.png",
				provider:'',
				nickname:'',
				password:null,
				openId:null,
				avatarUrl:null,
                mobile: '',
                shortCode: ''
            }
        },
        methods: {
			...mapMutations(['register']),
			getPhoneNumber(e){
				console.log(e.detail.errMsg)
				console.log(e.detail.iv)
				console.log(e.detail.encryptedData)
			},
			verify(){
				return true;
				if (this.mobile.length < 2) {
				    uni.showToast({
				        icon: 'none',
				        title: '昵称最短为 2 个字符'
				    });
				    return;
				}
				if (this.shortCode.length < 6) {
				    uni.showToast({
				        icon: 'none',
				        title: '密码最短为 6 个字符'
				    });
				    return;
				}
				return true;
			},
			verifyCode(mobile, shortCode){
				return true;
			},
            bindRegister() {
				let that = this;
                /**
                 * 客户端对账号信息进行一些必要的校验。
                 * 实际开发中，根据业务需要进行处理，这里仅做示例。
                 */
                if(!this.verify)return;
				//校验手机验证码
				if(!this.verifyCode(this.mobile, this.shortCode)){
					uni.showToast({
					    icon: 'none',
					    title: '手机验证码有误'
					});
					return;
				}
				//
// 				const data = {
// 				    mobile: this.mobile,
// 					password: this.$data.password
// 				}
				//
				let param = {
					nickname:this.$data.nickname,
					password:this.$data.password,
					avatarUrl:this.$data.avatarUrl,
					mobile: this.$data.mobile,
					'open_id_${this.$data.provider}': this.$data.openId
				};
				uni.request({
					url: this.$http.contextPath + 'account/register',
					method: 'POST',
					data:param,
					success: (res) => {
						console.log(res);
						if(this.$http.success(res)){
							that.success(res.data.data);
						}
					},
					fail: (e) => {
						uni.showToast({
							title:"服务器响应错误"
						})
					}
				})
            },
			success(account){
				this.register(account);
				uni.showToast({
				    title: '注册成功'
				});
				//跳转登录
				uni.redirectTo({
					url: '../login/login'
				});
			}
        },
		onLoad: function (option) { 
			this.$data.nickname = option.nickname;
			this.$data.provider = option.provider;
			this.$data.password = option.password;
			this.$data.openId = option.openId;
			this.$data.avatarUrl = option.avatarUrl;
		}
    }
